<style lang="less">

    .tatltdbig {
        border-bottom: 1px solid #e9eaec;
    }

    .tataltr {
        border: 1px solid #e9eaec;
        border-right: none;
        float: left;
        width: 100%;
        border-bottom: none;
    }

    .tatltd {
        border-right: 1px solid #e9eaec;
        width: 33.3%;

        float: left;
        text-align: center;
        padding: 5px 0;

    }
</style>
<template>
    <div class="layout">
        <Layout>
            <my-head @back="bacl"></my-head>
            <Content :style="{padding: '0 50px'}">
                <Row type="flex" justify="center" align="middle" class="code-row-bg">

                    <Col span="20" push="4" type="flex" justify="end">
                    <Row type="flex" justify="end" class="code-row-bg">
                        <span style="margin-right: 40px;font-size: 14px">入库单：{{head.sheet_no}}</span>
                        <span style="margin-right: 40px;font-size: 14px">供应商：{{head.publisher_no}}</span>
                        <span style="margin-right: 20px;font-size: 14px" v-text="head.dt!=undefined?'收货时间：'+head.dt.split('T')[0]:''"></span>
                    </Row>
                    </Col>
                    <Col span="4" pull="20">
                    <Breadcrumb :style="{margin: '20px 0'}">
                        <BreadcrumbItem to="/Inco">入库单</BreadcrumbItem>
                        <BreadcrumbItem>产品详情</BreadcrumbItem>
                    </Breadcrumb>
                    </Col>
                </Row>
                <Card style="padding: 0">
                    <div>
                        <Table @on-row-click="teblcli" :loading="lodaing" border stripe :columns="columns1"
                               :data="data1"
                               style="height: 520px;"></Table>
                    </div>
                    <Row type="flex" justify="center" align="middle" class="code-row-bg" style="margin-top: 16px;">
                        <Col span="8">
                        <Button :disabled="disabled" @click="tonyh" type="warning"
                                style="padding-left: 30px;padding-right: 30px;margin-right: 30px">
                            统一货架
                        </Button>
                        <Button :disabled="disabled" type="primary" style="padding-left: 30px;padding-right: 30px;"
                                @click="subinco">
                            <span>提交入库</span>
                        </Button>
                        </Col>
                        <Col span="16">

                        <Row type="flex" justify="end" class="code-row-bg">
                            <Col style="margin: 4px 15px 0  0;">
                            共<span style="color: #D72236;font-size: 18px;font-weight: 700"> {{total}} </span>条数据，有<span style="color: #D72236;font-size: 18px;font-weight: 700"> {{Untreated}} </span>条待上架产品
                            </Col>
                            <Page style="float: right;" @on-change="pagecli" :total="total" :page-size="10"></Page>
                        </Row>

                        </Col>
                    </Row>
                </Card>
            </Content>
            <Footer class="layout-footer-center" style="text-align: center">2011-2018 &copy; 怡乐仓库</Footer>
        </Layout>
        <Modal
                v-model="modal6"
                title="提示"
                :loading="loadinga"
                @on-ok="asyncOK"
                :styles="{width:'700px'}">
            <Row style="padding: 0 20px;font-size: 13px;">
                <Col span="6">
                送货单号：{{head.associate_noY}}</Col>
                <Col span="6">
                产品号：{{head.sheet_no}}</Col>
                <Col span="5">
                品种数量：{{head.pz_nums}}</Col>
                <Col span="7">
                收货时间：{{head!=''?head.dt.split('T')[0]:''}}
                </Col>
            </Row>
            <Row style="padding: 0 20px">
                <Col class="tatltdbig" span="24" style="text-align: center;padding: 100px 0;">
                统一架位：
                <Select v-model="model6" style="width:200px;margin-right: 10px;">
                    <Option v-for="item in cityList" :value="item.value" :key="item.value">{{ item.label }}</Option>
                </Select>
                <Checkbox v-model="single">覆盖已填写架位</Checkbox>
                </Col>
            </Row>
        </Modal>
        <Modal
                v-model="modal5"
                title="提示"
                :loading="loadingb"
                @on-ok="deslOK"
                class-name="vertical-center-modal"
                :styles="{width:'400px'}">
            <Row style="padding:0  30px">
                <Col span="24" style="font-size: 14px;margin-bottom: 12px">
                产品编号： {{desl.serial_no}}</Col>
                <Col span="24" style="font-size: 14px;margin-bottom: 12px">
                名称： {{desl.book_name}}</Col>
                <Col span="24" style="font-size: 14px;margin-bottom: 12px">
                类型： {{desl.booktype_name}}</Col>
                <Col span="24" style="font-size: 14px;margin-bottom: 12px">
                单位： {{desl.book_property}}</Col>
                <Col span="24" style="font-size: 14px;margin-bottom: 12px">
                规格： {{desl.bind_name}}</Col>
                <Col span="24" style="font-size: 14px;margin-bottom: 12px">
                数量： {{desl.num1}}</Col>

            </Row>
            <Row style="padding:0  30px;">
                <Row style="padding:12px 0;border: 1px dashed #e9eaec;border-left: none;border-right: none">
                    <Row>
                        <Col span="8" style="margin-bottom: 15px;">
                        <Input :disabled="disabled" @on-keyup="climod" v-model="model4" placeholder="数量" style="width: 80px;"></Input>
                        </Col>
                        <Col span="8" style="margin-bottom: 15px;">
                        <Select :disabled="disabled" v-model="model3" style="width:100px">
                            <Option v-for="item in cityList" :value="item.value" :key="item.value" :disabled="item.str">
                                {{ item.label }}
                            </Option>
                        </Select>
                        </Col>
                        <Col span="8" style="margin-bottom: 15px;text-align: center">
                        <Button type="dashed" @click="addLIS" :disabled="disabled">添加</Button>
                        </Col>
                    </Row>
                    <Table border :columns="columns2" :data="data2"></Table>
                </Row>
                <router-link
                        :to="{path:'IncoPhoto',query:{associate_noY:$route.query.sheet_no,SubAssociate_no:desl.serial_no}}">
                    <Button type="warning" style="margin-top: 15px">照片留档</Button>
                </router-link>
            </Row>
        </Modal>

        <Modal
                v-model="modal1"
                title="提示"
                @on-ok="titlOk"
                :loading="true">
            <p style="text-align: center;font-size: 13px;padding: 50px;">
                <Icon type="alert" size="16" style="margin-right: 5px;"></Icon>
                是否要将订单 <{{$route.query.sheet_no}}> 确认收货，一旦提交将无法修改
            </p>
        </Modal>
    </div>
</template>
<script type="text/ecmascript-6">
    let kno;
    export default {
        name: 'TakeVou',
        data () {
            return {
                modal1: false,
                model3: '',
                model4: '',
                columns2: [
                    {
                        title: '数量',
                        key: 'num'
                    },
                    {
                        title: '架位',
                        key: 'in_kno'
                    },
                    {
                        title: '操作',
                        key: 'sheet_no',
                        render: (h, params) => {
                            return h('div', [
                                h('Button', {
                                    props: {
                                        type: 'dashed',
                                        size: 'small',
                                        disabled:this.disabled
                                    },
                                    on: {
                                        click: () => {
                                            this.remove(params.index)
                                        }
                                    }
                                }, 'Delete')
                            ]);
                        }
                    }
                ],
                data2: [],
                cityList: [],
                model6: '',
                single: false,
                user: {},
                total: 1,
                page: 0,
                lodaing: true,
                loadinga: true,
                loadingb: true,
                value2: 0,
                modal6: false,
                modal5: false,
                kno: '',
                head: '',
                desl: '',
                value3: '',
                serial: '',
                Untreateda:'',
                Untreated:'',
                columns1: [
                    {
                        title: '产品编号',
                        key: 'serial_no'
                    },
                    {
                        title: '名称',
                        key: 'book_name',
                        width: '20%'
                    },
                    {
                        title: '分类',
                        key: 'booktype_name'
                    },
                    {
                        title: '规格',
                        key: 'bind_name'
                    },
                    {
                        title: '单位',
                        key: 'book_property'
                    },
                    {
                        title: '数量',
                        key: 'num1'
                    },
                    {
                        title: '货架',
                        key: 'kno',
                        render: function (h, i) {
                            var s = i.row.serial_no;
                            var list = [];
                            if (kno != null) {
                                for (var a = 0; a < kno.length; a++) {
                                    if (kno[a].serial_no == s) {
                                        list.push(kno[a].kno)
                                    }
                                }
                                return list.join('，')
                            }
                        }
                    },
                    {
                        title: '留档',
                        key: 'kno',
                        render: function (h, i) {
                            if (i.row.kno == '') {
                                return h('div', [
                                    h('Icon', {
                                        props: {
                                            type: 'ios-analytics-outline'
                                        },
                                        style: {
                                            fontSize: '16px'
                                        },
                                    }),
                                ]);
                            } else {
                                return h('div', [
                                    h('Icon', {
                                        props: {
                                            type: 'checkmark'
                                        },
                                        style: {
                                            fontSize: '16px'
                                        },
                                    }),
                                ]);
                            }
                        }

                    }
                ],
                data1: [],
                disabled: false,
            }
        },
        methods: {

            climod(){
                this.model4=this.model4.replace(/[^\d.]/g,'');
            },

            titlOk(){
                var v=this;
                v.Axios.post('http://116.247.103.119/imt16webapi/sheets/ConfirmOnShelf', v.Qs.stringify({
                    sheet_no: v.$route.query.sheet_no
                })).then(function (res) {
                    if(res.data.error != 0){
                        v.$Message.error(res.data.errorMsg)
                    }else{
                        v.$Message.success('提交成功');
                        v.disabled = true;
                        v.modal1 = false;
                    }
                })
            },

            //提交入库弹窗
            subinco(){
                var v = this;
                v.Axios.post('http://116.247.103.119/imt16webapi/sheets/NextOnShelf', v.Qs.stringify({
                    sheet_no: v.$route.query.sheet_no
                })).then(function (res) {
                    if (res.data.error == 0) {
                        v.modal1 = true
                    } else {
                        v.$Message.config({
                            duration: 3
                        });
                        var arr = res.data.errorMsg.split('：')[1].split('、');
                        var tim = 0;
                        for (var i = 0; i < arr.length; i++) {
                            (function (se) {
                                setTimeout(function () {
                                    v.$Message.error(arr[se]);
                                }, tim);
                                tim = tim + 300;
                            }(i))
                        }
                    }
                }).catch(function () {
                    v.$emit('error', '对方不想说话，并且向你抛出了一个异常');
                })
            },

            remove (index) {
                this.data2.splice(index, 1);
                this.peid();
            },

            deslOK(){
                var v = this;
                var data2 = v.data2;
                var knolas = [];
                for (var i = 0; i < data2.length; i++) {
                    knolas.push({
                        kno: data2[i].in_kno,
                        num: data2[i].num
                    })
                }
                v.Axios.post('http://116.247.103.119/imt16webapi/sheets/OnShelfKnos', v.Qs.stringify({
                    sheet_no: v.$route.query.sheet_no,
                    serial_no: v.desl.serial_no,
                    knonumlist: JSON.stringify({"KnoNum": knolas})
                })).then(function (res) {
                    if (res.data.error != 0) {
                        v.$Message.error(res.data.errorMsg);
                        v.loadingb = false;
                    } else {
                        v.$Message.success('提交成功');
                        v.InStock(v.page);
                        v.modal5 = false;
                    }
                    setTimeout(function () {
                        v.loadingb = true;
                    }, 0)
                })
            },

            //添加产品架位
            teblcli(i){
                var v = this;
                v.desl = i;
                v.modal5 = true;
                v.Axios.post('http://116.247.103.119/imt16webapi/sheets/getOnshelfKno', v.Qs.stringify({
                    sheet_no: v.$route.query.sheet_no,
                    serial_no: i.serial_no
                })).then(function (res) {
                    v.modal5 = true;
                    v.data2 = [];
                    v.model4 = '';
                    v.model3 = '';
                    if (res.data.data[0].in_kno != '000') {
                        v.data2 = res.data.data;
                    }
                    v.peid();
                }).catch(function () {
                    v.$emit('error', '对方不想说话，并且向你抛出了一个异常')
                })
            },

            //产品列表
            InStock(num){
                var v = this;
                v.lodaing = true;
                v.Axios.post('http://116.247.103.119/imt16webapi/sheets/GetOnShelfNew', v.Qs.stringify({
                    sheet_no: v.$route.query.sheet_no,
                    pagenum: num,
                    pagesize: 10
                })).then(function (res) {
                    if(res.data.error != 0){
                        v.$emit('error', String(res.data.errorMsg))
                    }else{
                        kno = res.data.data.knolist;
                        v.data1 = res.data.data.detail;
                        v.total = res.data.total;
                        v.Untreated = res.data.Untreated;
                        v.lodaing = false;
                        v.head = res.data.data.head[0];
                        res.data.data.head[0].status != '2' ? v.disabled = false : v.disabled = true;
                    }
                }).catch(function (r) {
                    v.$emit('error', '对方不想说话，并且向你抛出了一个异常')
                })
            },

            //验证弹窗架位数量是否填写
            addLIS(){
                var v = this;
                if (v.model4 == '') {
                    v.$Message.error('新增数量不可为空')
                } else if (v.model3 == '') {
                    v.$Message.error('新增架位不可为空')
                } else {
                    v.data2.push({
                        num: v.model4,
                        in_kno: v.model3,
                        sheet_no: '1'
                    });
                    v.model4 = '';
                    v.model3 = '';
                    v.peid()

                }
            },

            //统一货架
            asyncOK(){
                var v = this;
                var che = 0;
                v.single == false ? che = 0 : che = 1;
                v.Axios.post('http://116.247.103.119/imt16webapi/sheets/OnShelfAll', v.Qs.stringify({
                    sheet_no: v.$route.query.sheet_no,
                    kno: v.model6,
                    isall: che
                })).then(function (res) {
                    if (res.data.error != 0) {
                        v.$emit('error', String(res.data.errorMsg))
                        v.loadinga = false;
                    } else {
                        v.InStock(1);
                        v.modal6 = false;
                        v.$Message.success('统一架位成功');
                    }
                    setTimeout(function () {
                        v.loadinga = true;
                    }, 0)
                }).catch(function (r) {
                    v.$emit('error', '对方不想说话，并且向你抛出了一个异常')
                })
            },

            //统一货架弹窗
            tonyh(){
                this.modal6 = true;
            },

            //架位信息
            knolis(){
                var v = this;
                v.Axios.post('http://116.247.103.119/imt16webapi/sheets/GetKno', v.Qs.stringify({
                    bookstore_name: v.user.bookstore_name,
                })).then(function (res) {
                    if (res.data.error != 0) {
                        v.$emit('error', String(res.data.errorMsg))
                    } else {
                        var a = res.data.data;
                        v.cityList = [];
                        for (var i = 0; i < a.length; i++) {
                            v.cityList.push({value: a[i].kno, label: a[i].kno})
                        }
                    }
                }).catch(function (r) {
                    v.$emit('error', '对方不想说话，并且向你抛出了一个异常')
                })
            },

            peid(){
                var v = this;
                var s = v.data2;
                var c = v.cityList;
                for (var e = 0; e < c.length; e++) {
                    c[e].str = false
                }
                for (var i = 0; i < s.length; i++) {
                    for (var d = 0; d < c.length; d++) {
                        if (c[d].label == s[i].in_kno) {
                            c[d].str = true;
                        }
                    }
                }
                v.cityList = c;
            },

            //产品详情弹窗
            pagecli(i){
                this.page = i;
                this.InStock(this.page)
            },

            bacl(i){
                this.user = i
            }
        },
        mounted(){
            this.knolis();
            this.InStock(1);

        },
    }

</script>

<!-- Add "scoped" attribute to limit less to this component only -->
<style lang="less">
</style>



